<template>
    <div>
        <div class="setting-main">
            <div class="setting-item setting-title">
                <h2>
                    <a @click="backIndex()"></a>
                    
                </h2>
             </div>

            <div class="setting-item setting-phone">
                <div class="item-title">
                    <h3 class="title"></h3>
                </div>
                <div class="item-content">
                    <div class="telephone-content">
                        <p class="txt"></p>
                        <a class="link"></a>
                        <span class="telephone-Num">{{$store.state.telephone}}</span>
                     </div>
                 </div>
             </div>

            <div class="setting-item setting-oauth">
                <div class="item-title">
                    <h3 class="title"></h3>
                 </div>
                <div class="item-content">
                    <div class="oauth-content">
                        <p class="txt">OTOD</p>
                        <p class="connection">
                            <span class="icon icon-xina">
                                <span class="connection-name txt"></span>
                                <a>()</a>
                             </span>
                            <span class="icon icon-qq">
                                <span class="connection-name txt">QQ</span>
                                <a>()</a>
                             </span>
                            <span class="icon icon-weixin">
                                <span class="connection-name txt"></span>
                                <a>()</a>
                             </span>
                         </p>
                     </div>
                 </div>
             </div>

            <div class="setting-item setting-interesting">
                <div class="item-title">
                    <h3 class="title"></h3>
                 </div>
                <div class="item-content">
                    <div class="interesting-content">
                        <p class="txt"></p>
                        <a class="link"></a>
                     </div>
                 </div>
             </div>

            <div class="setting-item setting-info">
                <div class="item-title">
                    <h3 class="title"></h3>
                 </div>
                <div class="item-content">
                    <div class="interesting-content">
                        <p class="txt"></p>
                    </div>
                    <div class="info-head">
                        <div class="user-head">
                            <img :src="$imageUrl + $store.state.headImage"/>
                         </div>
                        <div class="user-arrow"></div>
                        <div class="user-uploader">
                            <!-- <div class="uploader-arrow"></div> -->
                            <el-upload
                              class="img-uploader"
                              action="http://localhost:8081/user/saveImg"
                              accept="image/jpeg,image/gif,image/png"
                              :show-file-list="false"
                              :on-success="handleAvatarSuccess"
                              :before-upload="beforeAvatarUpload">
                                <img v-if="imageUrl" :src="$imageUrl + imageUrl" class="avatar">
                                <i v-else class="el-icon-plus"></i>
                            </el-upload>
                         </div>
                     </div>
                    <div class="info-content">
                        <ul>
                            <li class="nes-li"><h6> :</h6><input type="text" name="nickname" v-model="userInfo.nickname" placeholder="" @blur="nicknameCheck"/></li>
                            <li><h6> :</h6><input type="text" name="position" v-model="userInfo.position" placeholder="" /></li>
                            <li class="nes-li"><h6> :</h6>
                                <el-radio-group v-model="userInfo.sex">
                                    <el-radio :label="1"></el-radio>
                                    <el-radio :label="2"></el-radio>
                                    <el-radio :label="3"></el-radio>
                                 </el-radio-group>
                             </li>
                            <li><h6> :</h6><input type="number" name="age" min="0" step="1" v-model="userInfo.age" /></li>
                            <li><h6> :</h6><input type="text" name="address" v-model="userInfo.address" placeholder="" /></li>
                            <li><h6> </h6><input type="email" name="email" v-model="userInfo.email" placeholder=""  @blur="emailCheck"/></li>
                            <li>
                              <h6>   </h6>
                              <el-input
                                class="input-area"
                                type="textarea"
                                :rows="2"
                                v-model="credo">
                              </el-input>
                            </li>
                        </ul>
                    </div>
                 </div>
             </div>

            <div class="setting-save">
                <div class="m-set">
                    <div class="btns">
                        <a class="w-btn save" @click="saveSetting">&nbsp;&nbsp;</a>
                     </div>
                </div>
             </div>
        </div>
 <!--        <div class="setting-footer"></div> -->
    </div>
</template>

<script >
export default {
  name: 'Setting',
  data () {
    return {
      credo: '',
      imageUrl: '',
      newImageUrl: '',
      userInfo: {
      },
      newUserInfo: {},
      error: {
        nickname: true,
        email: true
      }
    }
  },
  mounted () {
    // GET user infomation
    this.initialInfo()
  },
  methods: {
    initialInfo () {
      this.$axios({
        method: 'get',
        url: this.$url + '/user/getAllInfo',
        params: {
          access_token: this.$getCookie('otod_access_token')
        }
      }).then(function (response) {
        this.userInfo = response.data
        if (this.userInfo.sex === '') {
          this.userInfo.sex = 1
        } else if (this.userInfo.sex === '') {
          this.userInfo.sex = 2
        } else {
          this.userInfo.sex = 3
        }
      }.bind(this))
    },

    // headImage upload
    handleAvatarSuccess (res, file) {
      if (res === 'Error!') {
        this.$message.error('')
      } else if (res === 'Empty!') {
        this.$message.error('')
      } else {
        this.imageUrl = res
      }
    },

    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/gif'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error(' JPG/PNG/GIF !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return isJPG && isLt2M
    },

    // check info
    nicknameCheck () {
      if (this.userInfo.nickname !== '') {
        this.$axios({
          method: 'get',
          url: this.$url + '/user/getSimpleByNickname',
          params: {
            nickname: this.userInfo.nickname
          }
        }).then(function (response) {
          if (response.data.nickname !== null) {
            this.error.nickname = true
          }
        })
      }
    },
    emailCheck () {
      if (this.userInfo.email !== '') {
        this.error.email = !/^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/.test(this.userInfo.email)
        this.$message.error('')
      }
    },

    backIndex () {
      this.$notify({
        title: '',
        message: '',
        position: 'bottom-right'
      })
      this.$router.push({ name: 'Person', params: { page: 'home' } })
    },

    saveSetting () {
      if (this.userInfo.nickname === '') {
        this.$message.error('')
        return
      }

      if (!this.error.email) {
        this.$message.error('')
        return
      }

      this.newUserInfo = this.userInfo

      // add new headImage url
      if (this.imageUrl !== '') {
        this.newUserInfo.headImage = this.imageUrl
      }

      // change sex value
      switch (this.userInfo.sex) {
        case 1: this.newUserInfo.sex = ''
          break
        case 2: this.newUserInfo.sex = ''
          break
        case 3: this.newUserInfo.sex = ''
          break
      }

      this.$axios({
        method: 'post',
        url: this.$url + '/user/setAllInfo',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + this.$getCookie('otod_access_token')
        },
        dataType: 'json',
        data: this.newUserInfo
      }).then(function (response) {
        if (response.data === 'success') {
          this.$store.commit('initialName', this.newUserInfo.nickname)
          this.$store.commit('initialHead', this.newUserInfo.headImage)
          this.$notify({
            title: '',
            message: '',
            position: 'bottom-right'
          })
          this.$router.replace({ name: 'Person', params: { page: 'home' } })
        }
      }.bind(this))
    }
  }
}
</script>

<style>
    @import '../assets/css/SettingPage.css'
</style>



