<template>
  <div>
      <el-container>
        <el-main>
          <!---->
            <div class="search">
              <el-row>
                <el-col :span=10 :offset="14">
                  <div class="search_form">
                    <el-form :inline="true"  class="demo-form-inline">
                      <el-form-item label="">
                        <el-input v-model="product_key" placeholder="" clearable></el-input>
                      </el-form-item>
                      <el-form-item>
                        <el-button type="primary" @click="changepage('/market?product_key='+product_key)"></el-button>
                      </el-form-item>
                    </el-form>
                  </div>
                </el-col><!-- /.col-lg-6 -->
              </el-row><!-- /.row -->
            </div>

            <div id="position">
              <el-row>
                <el-col :span=8 :push="4">
                  <p><el-button type="text" @click="changepage('/market')"></el-button> -> </p>
                </el-col>
              </el-row>
            </div>


            <!---->
            <div id="product">
              <el-row type="flex" justify="center">
                <el-col :span=6><div class="grid-content text-center"><h3  style="font-family: 'Microsoft YaHei';">{{product.product_name}}</h3></div></el-col>
              </el-row>
              <br><br>
              <el-row>
                <el-col :span=5 :offset=4>
                  <!---->
                  <div id="product_img ">
                  <!---->
                    <div id="img-top">
                      <a @click="handlePictureCardPreview(show_picture)">
                        <img class="thumbnail" :src="show_picture" alt="" width="420px" height="400px"/>
                      </a>
                    </div>
                    <el-dialog :visible.sync="dialogVisible">
                      <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                    <!---->
                    <hr>

                    <div id="img-box">
                      <template v-for="item in img_url">
                        <el-button :key="item" @click="toshow(item.img_url)" circle>
                          <img class="thumbnail" :src="item.img_url" alt="" width="45px" height="45px"/>
                        </el-button>
                      </template>
                    </div>
                  </div>
                </el-col>

                <!---->

                <div id="product_attribute">
                  <br>
                  <br>
                  <el-col :span=5 :offset=2>
                    <div class="top">
                      <ul class="list-group">
                        <li class="list-group-item"><span > <strong> {{product.product_price}} </strong></span></li>
                        <li class="list-group-item"><span > <strong> 10.00 </strong></span></li>
                        <li class="list-group-item"><span > <strong style="color: blue">{{product.product_encoding}}</strong></span></li>
                      </ul>
                      <hr>
                    </div>
                    <div class="middle">
                      <ul class="list-group">
                        <li class="list-group-item"><span><strong></strong> </span></li>
                        <li class="list-group-item"><span>: <strong>{{product.product_stock}}</strong></span></li>
                        <li class="list-group-item"><span><strong>{{product.product_day_from}}:00 ~ {{product.product_day_to}}:00</strong> </span><span class="pull-right">:<strong>{{product.product_life}}</strong></span></li>
                      </ul>
                    </div>
                    <hr>
                    <br>
                    <br><br>
                    <br>
                    <br>
                    <div class="buttom">
                      <!---->
                      <dl>
                        <dt style="float:left;">
                          
                        </dt>
                        <dd>
                          <a href="" ><img :src="component_img.security" alt="" width="20px" height="20px"></a>
                        </dd>
                        <dd>
                          <span><img :src="component_img.security" alt="" width="20px" height="20px"></span>
                          <span></span>
                        </dd>
                      </dl>
                      <hr>
    <br>
    <br>


                      <!---->
                      <div id="add_cart" class="pull-right">
                        <el-button @click="checklogin()">
                          <span><img :src="component_img.add_cart" alt="" width="40px" height="40px"></span>
                          <span></span>
                        </el-button>
                        <el-dialog
                          title=""
                          :visible.sync="payfor_wrong"
                          width="30%">
                          <span></span>
                          <span slot="footer" class="dialog-footer">
                            <el-button type="primary" @click="payfor_wrong = false"> </el-button>
                          </span>
                        </el-dialog>
                      </div>
                    </div>
                  </el-col>
                </div>

                <!---->
                <!--  <div id="seller_message">
                    <el-col :span=3 :push="8">
                      <ul class="list-group">
                        <li class="list-group-item">
                          <span ><img src="img/user_id.jpg" alt="" width="60px" height="60px"></span>
                          <span  style="display:inline-block;float:right;"><h4><strong></strong></h4></span>
                        </li>
                        <li class="list-group-item">
                          <p><span >:</span><span class="pull-right">: <strong>36 </strong></span></p>
                          <p><span class="pull-right">: <strong>35</strong></span></p>
                          <div style="clear:both"></div>
                        </li>
                        <li class="list-group-item">
                          <p><span >:</span><span class="pull-right">: <strong>17 </strong></span></p>
                          <p><span class="pull-right">: <strong>17</strong></span></p>
                          <div style="clear:both"></div>
                        </li>
                      </ul>
                    </el-col>
                  </div>-->
                </el-row>
              </div>

            <div id="detail">
              <el-row>
                <el-col :push="4" :span=16>
                  <el-tabs @tab-click="handleClick">
                    <el-tab-pane label="">
                      <div id="product_description" >
                        <el-col :push="1" span=22>
                          <!--  -->
                          <p>{{product.product_description}}</p>
                        </el-col>
                      </div>
                    </el-tab-pane>

                    <el-tab-pane label="">
                      <div id="liucheng" >
                        <el-col :push="1" span=22>
                          <img :src="component_img.liucheng" alt="">
                        </el-col>
                      </div>
                    </el-tab-pane>
                  </el-tabs>
                </el-col>
              </el-row>
            </div>
        </el-main>
      </el-container>
  </div>
</template>

<script>
export default {
  mounted () {
    this.findproduct()
  },
  data () {
    return {
      muid: 0,
      no_login: false,
      is_login: false,
      payfor_wrong: false,
      dialogImageUrl: '',
      dialogVisible: false,
      loading: true,
      product: [],
      show_picture: '',
      page_num: 1,
      product_list: [],
      product_key: '',
      total_page_num: 0,
      total_product_num: '',
      component_img:
      {
        security: 'http://localhost:8081/component/security.ico',
        add_cart: 'http://localhost:8081/component/add_cart.ico',
        liucheng: 'http://localhost:8081/component/liucheng.png'
      },
      img_url: {}
    }
  },
  methods: {
    search () {
      var t = this
      this.$axios({
        method: 'get',
        url: 'http://localhost:8081/market/search',
        dataType: 'json',
        params: {
          product_key: t.product_key,
          page_num: t.page_num
        }
      }).then(function (response) {
        t.product_list = response.data.products
        t.total_product_num = response.data.totalElements
        t.total_page_num = response.data.totalPages
        t.loading = false
      })
    },
    changepage (path) {
      this.$router.push(path)
    },
    findproduct () {
      var t = this
      this.$axios({
        method: 'get',
        url: 'http://localhost:8081/market/product',
        dataType: 'json',
        params: {
          product_id: t.$route.query.product_id
        }
      }).then(function (response) {
        t.product = response.data
        t.show_picture = t.product.product_img_url
        t.img_url = response.data.product_imgs
        console.log(t.product)
      })
    },
    payforit () {
      var t = this
      if (t.product.product_status !== 1) {
        t.payfor_wrong = true
      } else {
        t.changepage('/market/product/pay?product_id=' + t.product.product_id)
      }
    },
    checklogin () {
      var t = this
      t.$axios({
        method: 'post',
        url: 'http://localhost:8081/market/getmuser',
        dataType: 'json',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': 'Bearer ' + t.$getCookie('otod_access_token')
        }
      }).then(function (response) {
        t.payforit()
      })
    },
    handleClick (tab, event) {},
    toshow (src) {
      this.show_picture = src
    },
    handlePictureCardPreview (src) {
      this.dialogImageUrl = src
      this.dialogVisible = true
    }
  }
}
</script>

<style>
</style>



