<template>
    <el-col :xs="12" :sm="12" :md="12" :xl="12" :offset="4">
        <ul v-loading="loadingOrder">
          <!-- element v-for  <li>html messageorder for(m in message){ <li></li>} -->
            <li v-show="message.length != 0" v-for="m in message" :key="m.id">
                <el-row class="message-bottom" type="flex" justify="center">
                    <el-col :span="24">
                        <el-card shadow="hover" class="center-container-card">
                          <!-- v-if element   m.urgencymessage cstrut  -->
                            <div v-if="m.urgency == '4'" style="height: 3px; clear: both; width: 100%" class="bg-danger"></div>
                            <div v-if="m.urgency == '3'" style="height: 3px; clear: both; width: 100%" class="bg-warning"></div>
                            <div v-if="m.urgency == '2'" style="height: 3px; clear: both; width: 100%" class="bg-success"></div>
                            <div v-if="m.urgency == '1'" style="height: 3px; clear: both; width: 100%" class="bg-info"></div>
                            <!-- el-containerelement  http://element-cn.eleme.io/#/zh-CN  -->
                            <el-container class="bg-purple">
                                <el-aside class="aside-container" width="200px">
                                    <div>
                                        <div class="user-img">
                                            <i v-if="m.userinfoS == null" class="el-icon-menu" style="font-size: 60px;color: #409EFF"></i>
                                            <img v-if="m.userinfoS != null" class="user-img" :src="$url + '/images/' + m.userinfoS.headImage"/>
                                        </div>
                                        <div class="user-info">
                                          <!-- {{ }} printf -->
                                            <p v-if="m.userinfoS != null">{{ m.userinfoS.nickname }}</p>
                                            <!-- <p>Nothing</p> -->
                                        </div>
                                        <div style="clear: both"></div>
                                    </div>
                                    <div class="button-group">
                                      <p></p>
                                      <div style="height: 1px;background-color: #f5f5f5;margin-top: 10px;margin-bottom: 10px;"></div>
                                        <div style="margin-bottom: 10px;">
                                            <el-button type="danger" icon="el-icon-close" circle @click="openDialog(m.id)" @mouseover.native="show1 = !show1" @mouseout.native="show1 = !show1"></el-button>
                                            <transition name="el-zoom-in-left">
                                                <div v-show="show1 || sstatic1" class="commit-button-box bg-danger">
                                                    <span></span>
                                                </div>
                                            </transition>
                                            <div style="clear: both"></div>
                                        </div>
                                    </div>
                                </el-aside>
                                <el-container>
                                    <el-header class="header-container"><strong>{{ m.title }}</strong></el-header>
                                    <el-main><p class="order-content">{{ m.content }}</p></el-main>
                                </el-container>
                            </el-container>
                        </el-card>
                    </el-col>
                </el-row>
            </li>
            <el-card v-show="message.length == 0" shadow="hover" class="center-container-card">
              <p></p>
            </el-card>
        </ul>
        <el-dialog :visible.sync="showFlag">
          <el-container>
            <el-header class="po-header-container"></el-header>
            <el-main>
              <el-form :model="form">
                <el-form-item label="" label-width="80px">
                    <el-input type="textarea" :autosize="{ minRows: 4}" v-model="form.reason" required></el-input>
                </el-form-item>
                <el-form-item label="" label-width="10px">
                    <div><el-button type="danger" size="medium" plain></el-button></div>
                </el-form-item>
                <el-button type="danger" style="width: 100%;" size="medium" @click="open()"></el-button>
              </el-form>
            </el-main>
          </el-container>
        </el-dialog>
    </el-col>
</template>

<script>
export default {
  name: 'Service',
  data () {
    return {
      navSelected: 2,
      isCollapse: false,
      sstatic: false,
      show: false,
      sstatic1: false,
      show1: false,
      message: '',
      totalPages: 0,
      currentPage: 0,
      loadingOrder: 'true',
      size: 5,
      showFlag: false,
      ssId: '',
      form: {
        'reason': ''
      }
    }
  },
  mounted () {
    var t = this
    this.get('/runningOrders', {access_token: this.$getCookie('otod_access_token')}, function (response) {
      t.message = response.data
      t.loadingOrder = false
    })
  },
  methods: {
    getAllRunningsOrders () {
      var t = this
      t.loadingOrder = true
      this.$axios({
        url: t.$url + '/runningOrders',
        method: 'get',
        params: {
          access_token: this.$getCookie('otod_access_token')
        }
      })
        .then(function (response) {
          console.log(response)
          t.message = response.data
          console.log(t.message)
          t.loadingOrder = false
          console.log(t.totalPages)
        })
        .catch(function (error) {
          console.log(error.message)
        })
    },
    openDialog (id) {
      this.ssId = id
      this.showFlag = true
    },
    get (url, data, callback) {
      var t = this
      t.$axios({
        method: 'get',
        url: t.$url + url,
        params: data || ''
      })
        .then(callback)
    },
    open () {
      var t = this
      if (t.form.reason === '') {
        t.$message({
          showClose: true,
          message: '',
          type: 'warning'
        })
        return
      }
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
        roundButton: true,
        center: true
      }).then(function () {
        console.log(t.ssId)
        t.get('/cancelOrderS', {
          OrderId: t.ssId,
          reason: t.form.reason,
          access_token: t.$getCookie('otod_access_token')
        }, function (response) {
          console.log(response)
          if (response.data === 'order canceled') {
            t.$message({
              showClose: true,
              message: '',
              type: 'success'
            })
          } else if (response.data === 'null Order') {
            t.$message({
              showClose: true,
              message: '',
              type: 'warning'
            })
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}
</script>

<style>
    @import '../../assets/css/OrderList.css'
</style>



